import { defaults, has } from 'lodash/fp'
import { inflate } from 'pako'
import { buffers } from 'redux-saga'
import { actionChannel, call, put, select, take, takeEvery } from 'redux-saga/effects'
import { getConnectionInfo } from '../../../auth/interface/selectors'
import { platforms } from '../../../constants'
import { getLogManager } from '../../../logs'
import * as actions from '../../interface/actions'
import * as actionTypes from '../../interface/actionTypes'
import { getNotificationConfig, getNotificationsInfo } from '../../interface/selectors'
import * as requests from '../requests'

// Get the logger
const log = getLogManager().getLogger('NOTIFICATION')

/**
 * Unregister for android push notification.
 * @method enableWebsocketChannelEntry
 */
export function * enableWebsocketChannelEntry () {
  yield takeEvery(actionTypes.ENABLE_NOTIFICATION_CHANNEL, enableWebsocketChannel)
}

function * enableWebsocketChannel (action) {
  if (action.payload.enable) {
    // TODO: If websockets are not connected, connect them here.
  }

  // TODO: Handle possible error case when connecting websockets.
  //      Otherwise, plain dispatch to update state.
  yield put(
    actions.enableNotificationChannelFinish(action.meta.channel, {
      params: action.payload
    })
  )
}

const INITIAL_BUFFER_SIZE = 10

export function * processNotification () {
  const config = yield select(getNotificationConfig)
  var idCache = []
  /**
   * Checks whether a notification has already been received,
   *      judging by its event ID.
   * @method isDuplicate
   * @param  {string} id
   * @return {boolean}
   */
  function isDuplicate (id) {
    if (idCache.indexOf(id) !== -1) {
      return true // duplicate.
    } else {
      // Not a duplicate.
      idCache.push(id)
      if (idCache.length > config.idCacheLength) {
        idCache.shift()
      }

      return false
    }
  }

  const externalNotifications = yield actionChannel(
    actionTypes.PROCESS_NOTIFICATION,
    buffers.expanding(INITIAL_BUFFER_SIZE)
  )
  while (true) {
    const action = yield take(externalNotifications)

    // Only process notifications from enabled channels, ie. "silence" the channel.
    let channel = yield select(getNotificationsInfo, action.meta.channel)
    if (!channel.channelEnabled) {
      log.debug('Notification received on disabled channel. Ignoring it.', action.meta.channel)
      continue
    }

    // Find the unique ID of the received notification.
    let notificationId
    switch (action.meta.platform) {
      case platforms.LINK:
        notificationId = action.payload.notificationMessage.eventId
        break
      case platforms.UC:
        // A Link notification can be in either the Link format or the SPiDR format (for calls).
        notificationId = action.payload.notificationMessage.id || action.payload.notificationMessage.eventId
        break
      case platforms.CPAAS:
        // CPaaS notifications do not have a consistent path to their ID.
        //    The ID is one level down in the object, but we need to dynamically
        //    find the key that its in.
        const notification = action.payload
        for (let key in notification) {
          if (notification[key].id) {
            notificationId = notification[key].id
            break
          }
        }
        if (!notificationId) {
          log.error('Received notification without a findable ID.', Object.keys(notification))
        }
        break
      default:
        log.debug('Received notification from unknown platform.')
    }

    let formattedPayload = action.payload
    if (has('payload.notificationMessage.sessionParams.sdpFormat', action)) {
      log.debug('Notification contains SDP. Normalizing.')
      formattedPayload = yield call(normalizeSDP, action.payload)
    }

    if (!isDuplicate(notificationId)) {
      // If we're in the PUSH-only notification mode and
      // this incoming notification is coming through a channel other than PUSH channel (e.g. WEBSOCKET) and
      // this notification is for an incoming call
      if (
        config.incomingCallNotificationMode === 'push-channel-only' &&
        action.meta.channel !== 'PUSH' &&
        formattedPayload.notificationMessage.eventType === 'call'
      ) {
        // Ignore this call-related notification, but at a minimum generate a log so that user is aware.
        log.warn(
          'Dropped incoming call notification with id: ' +
            notificationId +
            ' received through channel: ' +
            action.meta.channel +
            ' because current mode for handling notifications is set to: push-channel-only.'
        )
        continue
      }

      yield put(actions.notificationReceived(formattedPayload, action.meta.platform))
    } else {
      const error = new Error(`Notification id ${notificationId} is duplicate.`)
      // TODO: Tech-debt; this action should be a notificationReceived error action.
      //      But that requires all sagas listening for notifications to filter out
      //      error actions ..which requires their take() patterns changed, which
      //      is another tech-debt item.
      yield put(actions.processNotificationFinish(error))
    }
  }
}

/**
 * This function accepts a notification payload. If the payload is a spidr payload
 * and contains an sdpFormat that needs manipulating, it is done here.
 * @method normalizeSDP
 * @param payload A processNotification action payload
 * @returns payload A processNotification action payload
 */
function * normalizeSDP (payload) {
  // Copy the payload to not disturb the action.
  payload = { ...payload }

  if (payload.notificationMessage.sessionParams.sdpFormat === 'compressed') {
    log.debug('sdpFormat: compressed. Deflating compressed SDP...')
    // convert based64 encoded string into bytes
    const sdpCompressedBytes = atob(payload.notificationMessage.sessionParams.sdp)
    // uncompress the bytes
    const sdpUnCompressedBytes = yield call(inflate, sdpCompressedBytes)
    // convert uncompress bytes back into string
    const sdpString = String.fromCharCode.apply(null, new Uint16Array(sdpUnCompressedBytes))
    payload.notificationMessage.sessionParams.sdp = sdpString
    return payload
  } else if (payload.notificationMessage.sessionParams.sdpFormat === 'url') {
    log.debug('sdpFormat: url. Fetching SDP...')
    const connection = yield select(getConnectionInfo)
    const { pushRegistration } = yield select(getNotificationConfig)

    // If a push registration endpoint was configured, use that instead of default.
    if (pushRegistration) {
      connection.server = defaults(connection.server, pushRegistration)
      connection.protocol = defaults(connection.protocol, pushRegistration)
      connection.port = defaults(connection.port, pushRegistration)
      connection.version = defaults(connection.version, pushRegistration)
    }

    const response = yield call(requests.fetchSDP, connection, payload.notificationMessage.sessionParams.sdp)
    payload.notificationMessage.sessionParams.sdp = response.eventDataResponse.sdp
    return payload
  } else {
    log.debug(`Unknown sdpFormat received: ${payload.notificationMessage.sessionParams.sdpFormat}.`)
    return payload
  }
}
